import React, {Component} from "react";

import './app-header.css';

export default class AppHeader extends Component {

    state = {
        active: 0,
        info: this.props.data
    };

    clickOnLink = (e) => {
        e.preventDefault();
        const index = this.state.info.findIndex(el => el.data === e.target.text);
        this.props.onChangeTab(index);
        this.setState({
            active: index
        })
    };

    renderElements = () => {
        return this.state.info.map((item, index) => {
            const {key, data} = item;
            let liClassName = this.state.active === index ? "nav-link active" : "nav-link";
            return (
                <li key={key} className="nav-item">
                    <a className={liClassName} data-toggle="tab" href="#" onClick={this.clickOnLink}>{data}</a>
                </li>
            )
        });
    };

    render() {
        const elements = this.renderElements();
        return (
            <div className="header">
                <h2>Hello <small className="text-muted">alcoholic !</small></h2>
                <ul className="nav nav-tabs">
                    {elements}
                </ul>
            </div>
        )
    }
};
