import React, {Component} from "react";

import './coctail-item.css';
import CocapiServise from "../../services/cocapi";
import Spinner from "../spinner";
import ErrorIndicator from "../error-indicator";
import ErrorBoundry from "../error-boundry";

export default class CoctailItem extends Component {

    cocapi = new CocapiServise();

    state = {
        data: '',
        fragment: true,
        showRandom: this.props.random,
        loading: true,
        error: false
    };

    onError = () => {
        this.setState({
            error: true,
            loading: false
        })
    };

    componentDidMount() {
        if (this.state.showRandom) {
            this.cocapi.searchByRandom()
                .then(res => {
                    this.setState({
                        data: res.drinks[0],
                        loading: false
                    });
                })
                .catch(this.onError);
        } else {
            this.setState({
                data: this.props.data,
                loading: false
            });

        }
    }

    updateCoctail() {
        this.setState({
            data: this.props.data
        });
    };

    componentDidUpdate(prevProps) {
        if (!this.state.showRandom) {
            if (this.props.data.idDrink !== prevProps.data.idDrink) {
                this.updateCoctail();
            }
        }
    }

    showElemWithContent = (arr, id) => {
        let key = parseInt(id);
        const trueElements = arr.filter(item => item);
        return trueElements.map(elem => <li key={key++} className="card-item">{elem}</li>);
    };

    onWarningClose = () => {
        this.setState({
            fragment: false
        })
    };

    componentWillUnmount() {
        this.cocapi.abortController.abort();
    }

    render() {
        const {
            idDrink, strDrink, strGlass, strInstructions, strDrinkThumb, strIngredient1,
            strIngredient2, strIngredient3, strIngredient4, strIngredient5, strIngredient6, strIngredient7
        } = this.state.data;
        const {loading, fragment, error} = this.state;

        const glass = `Could be made in : ${strGlass}`;
        const picture = `${strDrinkThumb}`;
        let arrIngredients = [strIngredient1, strIngredient2, strIngredient3, strIngredient4, strIngredient5, strIngredient6, strIngredient7];
        const mainIngre = `Main ingredients :`;
        const elements = this.showElemWithContent(arrIngredients, idDrink);
        const itemData = {strDrink, glass, picture, strInstructions, mainIngre, elements};

        const hasData = !(loading || error);

        const spinner = loading ? <Spinner/> : null;
        const btnFragment = fragment ? <BtnFragment onClose={this.onWarningClose}/> : null;
        const itemBlock = hasData ? <ItemBlock data={itemData}/> : null;
        const errorBlock = error ? <ErrorIndicator/> : null;

        return (
            <ErrorBoundry>
                <div className="container">
                    {errorBlock}
                    {spinner}
                    {btnFragment}
                    {itemBlock}
                </div>
            </ErrorBoundry>
        )
    }
};

const BtnFragment = ({onClose}) => {
    return (
        <React.Fragment>
            <div className="alert alert-dismissible alert-success">
                <button type="button" className="close" data-dismiss="alert" onClick={onClose}>&times;</button>
                <strong>You're on the right way!</strong><br/>If you're a light to moderate drinker and you're
                healthy,
                you
                can probably continue to drink alcohol as long as you do so responsibly.
            </div>
        </React.Fragment>
    )
};

const ItemBlock = ({data}) => {
    const {strDrink, glass, picture, strInstructions, mainIngre, elements} = data;
    return (
        <div className="card mb-3">
            <h3 className="card-header">{strDrink}</h3>
            <div className="card-body" id="center">
                <h5 className="card-title">{glass}</h5>
            </div>
            <img className="img-style"
                 src={picture}
                 alt="snapshot of a coctail">
            </img>
            <div className="card-body">
                <p className="card-text">{strInstructions}</p>
                <div>
                    <p className="card-body-title">{mainIngre}</p>
                    <ul className="card-list">
                        {elements}
                    </ul>
                </div>
            </div>
        </div>
    )
};
