import React, {Component} from "react";

import './coctail-list.css';
import CocapiServise from "../../services/cocapi";
import CoctailItem from "../coctail-item";
import ShortItem from "../short-item";
import ErrorBoundry from "../error-boundry";

export default class CoctailList extends Component {

    cocapi = new CocapiServise();

    state = {
        data: [],
        selectedCoctail: null,
        short: false,
        searchPhrase: ''
    };

    showSelectedItem = (coctail) => {
        if (!this.state.short) {
            return (
                <CoctailItem random={false} data={coctail}/>
            )
        } else {
            return (
                <ShortItem data={coctail}/>
            )
        }

    };

    onSelectedCoctail = (coctail) => {
        this.setState({
            selectedCoctail: this.showSelectedItem(coctail)
        })
    };

    renderElements = (arr) => {
        return arr.map((item) => {
            const label = this.props.renderItem(item);
            const {idDrink} = item;
            return (<li key={idDrink} onClick={() => this.onSelectedCoctail(item)}
                        className="list-group-item d-flex justify-content-between align-items-center left__item">{label}</li>)
        });
    };

    onInputChange = (e) => {
        e.preventDefault();
        const phrase = (e.target.value);
        this.setState({
            searchPhrase: phrase
        });
    };

    errorObject = {
        strDrink: 'CANNOT FIND SUCH INGREDIENT !!!',
        idDrink: 999111,
        strDrinkThumb: null
    };

    onResLoaded = (receivedData, isShort) => {
        if (receivedData === undefined) {
            receivedData = {drinks: []};
            receivedData.drinks = [this.errorObject];
        }
        if (receivedData.drinks === null) {
            receivedData.drinks = [this.errorObject];
        }
        if (isShort === true && false) {
            receivedData.drinks = [this.errorObject];
        }
        this.setState({
            data: receivedData.drinks,
            short: isShort
        })
    };

    onInputSubmit = (e) => {
        e.preventDefault();
        if (this.props.search === "byIngredient") {
            this.cocapi.searchByIngredient(this.state.searchPhrase)
                .then(res => this.onResLoaded(res, true))
                .catch(err => console.error(err));
        } else {
            this.cocapi.searchByName(this.state.searchPhrase)
                .then(res => this.onResLoaded(res, false))
                .catch(err => console.error(err));
        }
        this.setState({
            searchPhrase: ''
        })
    };

    componentWillUnmount() {
        this.cocapi.abortController.abort();
    }

    render() {
        const elements = this.renderElements(this.state.data);
        const selected = !this.state.selectedCoctail ? 'Please, select item' : this.state.selectedCoctail;
        const haveSearchPhrase = this.state.data.length >= 1 ?
            <Container data={elements} isSelected={selected}/> : null;
        const whichIs = this.props.search === "byIngredient" ? `ingredient` : `coctail`;

        return (
            <ErrorBoundry>
                <div>
                    <form className="form-group" onSubmit={this.onInputSubmit}>
                        <label className="col-form-label col-form-label-lg"
                               htmlFor="inputLarge">{`Please, choose a ${whichIs} !`}</label>
                        <input className="form-control form-control-lg" type="text" placeholder="Right here !!!"
                               id="inputLarge" onChange={this.onInputChange} value={this.state.searchPhrase}/>
                    </form>
                    {haveSearchPhrase}
                </div>
            </ErrorBoundry>
        )
    }
}

const Container = ({data, isSelected}) => {

    return (
        <div className="private-container">
            <div className="left">
                <ul className="left__list">
                    {data}
                </ul>
            </div>
            <div className="right">
                {isSelected}
            </div>
        </div>
    )
};