export default class CocapiServise {

    abortController = new AbortController();
    _apiBase = `https://www.thecocktaildb.com/api/json/v1/1/`;

    async getResourse(url) {
        const result = await fetch(`${url}`, {signal: this.abortController.signal});
        if (!result.ok) {
            throw new Error(`Cannot connect ${url}, received ${result.status}`);
        }
        return await result.json();
    }

    async searchByName(name) {
        return await this.getResourse(`${this._apiBase}search.php?s=${name}`);
    }

    async searchByIngredient(ingredient) {
        return await this.getResourse(`${this._apiBase}filter.php?i=${ingredient}`)
            .catch(err => {});
    }

    async searchByRandom() {
        return await this.getResourse(`${this._apiBase}random.php`);
    }
}
