import React, {Component} from "react";
import '../../services/cocapi';
import './app.css';
import AppHeader from "../app-header"
import CoctailItem from "../coctail-item";
import CoctailList from "../coctail-list";
import ErrorIndicator from "../error-indicator";

import {CocapiProvider} from "../cocapi-service-context";
import CocapiServise from "../../services/cocapi";

export default class App extends Component {

    cocapiService = new CocapiServise();

    state = {
        show: 0,
        hasError: false
    };

    btns = [{key: 23123123, data: 'Get lucky!'}, {key: 31231312, data: 'Search by coctail'}, {
        key: 95345953,
        data: 'Search by ingredient'
    }];

    onChangedTab = (idx) => {
        this.setState({
            show: idx
        })
    };

    componentDidCatch(error, info) {
        console.log(info);
        this.setState({
            hasError: true
        })
    }

    render() {
        if (this.state.hasError) {
            return <ErrorIndicator/>
        }

        const showRandom = this.state.show === 0 ? <CoctailItem random={true}/> : null;
        const showCoctailSearch = this.state.show === 1 ? <CoctailList search="byName" renderItem={(item) => (
            <span>{item.strDrink}</span>)}/> : null;
        const showIngredientSearch = this.state.show === 2 ?
            <CoctailList search="byIngredient" renderItem={(item) => (<span>{item.strDrink}</span>)}
                         short={true}/> : null;
        return (
            <div className='container mobile'>
                <CocapiProvider value={this.cocapiService}>
                    <AppHeader data={this.btns} onChangeTab={this.onChangedTab}/>
                    {showRandom}
                    {showCoctailSearch}
                    {showIngredientSearch}
                </CocapiProvider>
            </div>
        )
    }
}